*{
  margin: 0;
  max-width: 800px;
  margin: auto;
}
.item{
  display: flex;
  background: rgba(224,224,224,1);
  justify-content: space-around;
  /* width: 50%; */
  align-items: center;
  height: 50px;
  margin: 2px 0;
}
.item > p{
  flex: 0.15;
}
.nav{
  display: flex;
  height: 30px;
  align-items: center;
  justify-content: space-around;
  background: chocolate;
  padding: 10px;
}
.nav button{
  background: orange;
  height: 30px;
  border-radius: 19%;
  cursor: pointer;
}
.form{
  display: flex;
  flex-direction: column;
  padding: 30px;
  height: 200px;
  justify-content: space-evenly;
  align-items: center;
  /* max-width: 1000px; */
}
.form div{
  /* background-color: chocolate; */
  width: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form div input{
  flex:0.5;
  margin: auto;
}
.form div label{
  flex:0.5;
}
.clear{
  max-width: 100px;
  display: flex;
  align-items: center;
  margin-top: 3px;
  cursor: pointer;
}
